import React from 'react';
//import './App.css';
import './basic.css';
//import cardex from './img/B16-001SRplus_SAMPLE.png';
// Color Icons
import symbolR from './img/colors/Red_(Cipher).png';
import symbolB from './img/colors/Blue_(Cipher).png';
import symbolP from './img/colors/Purple_(Cipher).png';
import symbolY from './img/colors/Yellow_(Cipher).png';
import symbolN from './img/colors/None_(Cipher).png';
import symbolG from './img/colors/Green_(Cipher).png';
import symbolBr from './img/colors/Brown_(Cipher).png';
import symbolW from './img/colors/White_(Cipher).png';
import symbolBk from './img/colors/Black_(Cipher).png';
import symbolWB from './img/colors/WhiteBlack_(Cipher).png';
// Type Icons
import genM from './img/types/Male_(Cipher).png';
import genF from './img/types/Female_(Cipher).png';
import wepS from './img/types/Sword_(Cipher).png';
import wepL from './img/types/Lance_(Cipher).png';
import wepA from './img/types/Axe_(Cipher).png';
import wepB from './img/types/Bow_(Cipher).png';
import wepF from './img/types/Fang_(Cipher).png';
import wepT from './img/types/Tome_(Cipher).png';
import wepR from './img/types/Staff_(Cipher).png';
import wepD from './img/types/Dragonstone_(Cipher).png';
import wepK from './img/types/Dagger_(Cipher).png';
import wepG from './img/types/Fist_(Cipher).png';
import typeB from './img/types/Beast_(Cipher).png';
import typeD from './img/types/Dragon_(Cipher).png';
import typeF from './img/types/Flier_(Cipher).png';
import typeA from './img/types/Armored_(Cipher).png';
import typeS from './img/types/Mirage_(Cipher).png';
import typeM from './img/types/Monster_(Cipher).png';

// Card Data
import CipherDB from './cipherdb.json';

const Colors = {
  red: symbolR, yellow: symbolY, purple: symbolP, green: symbolG, none: symbolN,
  blue: symbolB, white: symbolW, black: symbolBk, whiteblack: symbolWB, brown: symbolBr
}
const ColorsList = Object.keys(Colors);
const ColorsIndex = {};
ColorsList.forEach((entry, i) => ColorsIndex[entry] = i);
const Types = {
  male: genM, female: genF,
  sword: wepS, lance: wepL, axe: wepA, bow: wepB, fang: wepF,
  tome: wepT, staff: wepR, dragonstone: wepD, knife: wepK, fist: wepG,
  beast: typeB, dragon: typeD, flier: typeF,
  armored: typeA, mirage: typeS, monster: typeM
}
const TypesList = Object.keys(Types);
const TypesIndex = {}; // Avoid Iterating for IndexOf every sort
TypesList.forEach((entry, i) => TypesIndex[entry] = i);

const Rules = {
  "Unlimited": {},
  "Standard": {
    banned: new Set([
      "B01", "B02", "B03", "B04",
      "P01", "P02", "P03", "P04",
      "S01", "S02", "S03", "S04", "S05", "S06"
    ])
  }
}

function getColorIcon(color, width) {
  return <img key={color+'ico'} src={Colors[color]} alt={color} width={width || ''} />;
}

function getTypeIcon(type, width) {
  return <img key={type+'ico'} src={Types[type]} alt={type} width={width || ''} />
}

function properCase(str) {
  if (typeof str === 'string' && str.length > 1)
  return str[0].toUpperCase()+str.substring(1).toLowerCase();
}

function formatCost(cost, promo) {
  return promo ? `${cost}(${promo})` : cost;
}

function formatRange(min, max) {
  if (!max || min === max) return min || '-';
  else return `${min}-${max}`;
}

function getCard(id) {
  return CipherDB[id];
}

function getAllCards() {
  return Object.keys(CipherDB).map(id => getCard(id));
}

function getLackeyExpanded(name) {
  return name.replace(/\(([FM])\)/, (match, p1) => `(${p1 === 'F' ? 'Female': 'Male'})`);
}

function getLackeyShortened(name) {
  return name.replace(/\((Fe)?[Mm]ale\)/, (match, p1) => `(${p1 ? 'F': 'M'})`);
}

function getLackeyName(id) {
  let card = getCard(id);
  if (!card) return;
  const lackeyName = getLackeyExpanded(card.name);
  // Hard-Coded Names
  switch (card.name) {
    case 'Risen': return `Risen, ${card.number.startsWith('B08')?'Defiled':'Grotesque'} Soldier ${card.number[7].toUpperCase()}`
    default:
  }
  if (card.rarity === "PRr") return `${lackeyName}, ${card.title} (RP)`;
  if (card.rarity === "PR") {
    let promos = getAllCards().filter(c => c.rarity === "PR" && c.name === card.name && c.title === card.title);
    return `${lackeyName}, ${card.title} (P${promos.indexOf(card) ? promos.indexOf(card)+1 : ''})`;
  }
  if (card.rarity.endsWith('+X')) return `${lackeyName}, ${card.title}+X`;
  if (card.rarity.endsWith('+')) return `${lackeyName}, ${card.title}+`;
  if (card.rarity === 'ST') {
    return `${lackeyName}, ${card.title}${Object.keys(CipherDB).filter(id => id.substring(0, 7) === card.number && getCard(id).rarity !== 'ST').map(id => '+').join('')}`;
  }
  return `${lackeyName}, ${card.title}`;
}

function convertLackeyName(fullname) { // Returns ID
  const parts = fullname.split(', ');
  let name = parts[0];
  let title = parts.slice(1).join(', ');
  if (name === "Risen") {
    if (title.includes('Defiled')) return `B08-047${title[title.length-1].toLowerCase()}N`;
    else return `B14-040${title[title.length-1].toLowerCase()}N`;
  }
  let dbName = getLackeyShortened(name);
  let Cards = getAllCards();
  // Rarity +X
  if (title.endsWith('+X')) {
    title = title.substring(0, title.length-2).trim();
    let lookup = Cards.filter(card => card.rarity.endsWith('+X') && card.title === title && card.name === dbName);
    if (lookup.length < 1) return '';
    return lookup[0].id;
  }
  // Card Ends w/ ++ | Very Few Case. Hardcoding.
  if (title.endsWith('++')) {
    switch (name) {
      case "Ninian": return 'B13-0027ST';
      case "Pent": return 'B13-0029ST';
      case "Louise": return 'B13-0031ST';
      default: console.log('Unknown ++', fullname); return '';
    }
  }
  // Card Ends w/ +
  if (title.endsWith('+')) {
    title = title.substring(0, title.length-1).trim();
    let lookup = Cards.filter(card => card.title === title && card.name === dbName && (card.rarity.endsWith('+') || card.rarity === 'ST'));
    if (lookup.length < 1) return '';
    return lookup[0].id;
  }

  // Promos (RP) (P) (P#)
  let match = title.match(/\((R?P\d?)\)$/);
  if (match) {
    title = title.substring(0, match.index).trim();
    let promoTag = match[1];
    if (promoTag === 'RP') {
      let lookup = Cards.filter(card => card.title === title && card.rarity === 'PRr' && card.name === dbName);
      if (lookup.length < 1) return '';
      return lookup[0].id;
    }
    if (promoTag[0] === 'P') {
      let lookup = Cards.filter(card => card.title === title && card.rarity === 'PR' && card.name === dbName);
      if (lookup.length < 1) return '';
	  let promo = lookup[(promoTag[1] || 1)-1];
	  if (promo) return promo.id;
	  console.log('Promo Not Found', fullname);
      return '';
    }
  }

  // Default
  let lookup = Cards.filter(card => card.title === title && card.name === dbName);
  if (lookup.length < 1) return '';
  return lookup[0].id;
}

function numInRange(num, rule) {
  // X = 0.1; Greater Than 0 but...
  num = num === "X" ? 0.1 : +num;
  let cmp = rule[0];
  let cmpN = +rule.substring(1);
  switch (cmp) {
    case "=": return num === cmpN;
    case "": return num !== cmpN;
    case ">": return num > cmpN;
    case "": return num >= cmpN;
    case "<": return num < cmpN;
    case "": return num <= cmpN;
    default: return false;
  }
}

function compareTypes(typeA, typeB) {
  let listA = Object.keys(typeA);
  let listB = Object.keys(typeB);
  let cmp = 0;
  for (let i = 0; cmp === 0 && (i < listA.length || i < listB.length); i++) {
    let valA = listA[i] ? TypesIndex[listA[i]] : -1;
    let valB = listB[i] ? TypesIndex[listB[i]] : -1;
    cmp = valA - valB;
  }
  return cmp;
}

function compareColors(listA, listB) {
  let cmp = 0;
  for (let i = 0; cmp === 0 && (i < listA.length || i < listB.length); i++) {
    let valA = listA[i] ? ColorsIndex[listA[i]] : -1;
    let valB = listB[i] ? ColorsIndex[listB[i]] : -1;
    cmp = valA - valB;
  }
  return cmp;
}

function compareRanges(rangeA, rangeB) {
  return rangeA[0] !== rangeB[0] ?
    rangeA[0] - rangeB[0] :
    rangeA[1] - rangeB[1];
}

function NumCompSelect(props) {
  return <select value={props.value} onChange={props.onChange}>
    <option>{"="}</option>
    <option>{""}</option>
    <option>{">"}</option>
    <option>{""}</option>
    <option>{"<"}</option>
    <option>{""}</option>
  </select>
}

function NumCompInput(props) {
  let exp = props.exp;
  let onChange = props.onChange;
  return <React.Fragment>
    <NumCompSelect
      value={exp[0]}
      onChange={onChange}
    />
    <input
      type="number"
      value={exp.substring(1)}
      min={props.min}
      max={props.max}
      step={props.step || 1}
      onChange={onChange}
      className={props.inputClass}
      id={props.inputId}
    />
  </React.Fragment>
}

function CardImg(props) {
  let id = props.id;
  let set = id.substring(0, id.indexOf('-'));
  let card = getCard(id);
  return <img
    src={'.'+(card ? `/cards/${set}/${id.replace('+', 'plus')}_SAMPLE.png` : '/cardback.jpg')}
    alt={id+(card ? ' '+card.name : '')}
  />
}

class CardList extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      sortField: '',
      sortDescending: false,
    }
    this.handleHeadClick = this.handleHeadClick.bind(this);
  }

  handleHeadClick(event) {
    console.log(event.target);
    let field = event.target.dataset['name'];
    if (field === this.state.sortField) return this.toggleSort();
    return this.setState({
      sortField: field,
      sortDescending: false
    });
  }

  toggleSort() {
    return this.setState({ sortDescending: !this.state.sortDescending });
  }

  compareFields(field, cardA, cardB) {
    if (typeof cardA === 'string') cardA = getCard(cardA);
    if (typeof cardB === 'string') cardB = getCard(cardB);
    if (!cardA || !cardB) return;
    let mod = this.state.sortDescending ? -1 : 1;
    let cmp = 0;
    switch (field) {
      case "type": cmp = compareTypes(cardA.type, cardB.type); break;
      case "range": cmp = compareRanges(cardA.range, cardB.range); break;
      case "name": cmp = ((cardA.name === cardB.name ? cardA.title > cardB.title : cardA.name > cardB.name) ? 1 : -1); break;
      case "color": cmp = compareColors(cardA.colors, cardB.colors); break;
      case "cost": cmp = (cardA.cost === cardB.cost ? numInRange(cardA.promo, '>'+cardB.promo) : cardA.cost > cardB.cost) ? 1 : -1; break;
      default: cmp = (this.getField(cardA.id, field) > this.getField(cardB.id, field) ? 1 : -1);
    }
    return cmp * mod;
  }

  onSelect(event) {
    let entry = event.target.parentElement;
    if (event.target.tagName === "LI") entry = event.target;
    if (event.target.tagName === "IMG") entry = entry.parentElement;
    let selected = '';
    if (entry.attributes['data-id']) selected = entry.attributes['data-id'].value;
    else console.log('No ID in Entry', event.target);
    return this.props.onSelect(selected);
  }

  onView(event) {
    let entry = event.target.parentElement;
    if (event.target.tagName === "LI") entry = event.target;
    if (event.target.tagName === "IMG") entry = entry.parentElement;
    let selected = '';
    if (entry.attributes['data-id']) selected = entry.attributes['data-id'].value;
    else console.log('No ID in Entry', event.target);
    return this.props.onView(selected);
  }

  getCardList() {
    return Object.keys(this.props.listData).filter(id => id in CipherDB);
  }

  getCardIDs() {
    return this.props.listData;
  }

  makeCol(field, inner) {
    return <span key={field} className={`${field.toLowerCase()}-col`}>{inner}</span>
  }

  makeHeadCol(field) {
    let sorted = this.state.sortField === field;
    let sortDown = this.state.sortDescending;
    return <span
      key={field}
      data-name={field}
      className={`${field.toLowerCase()}-col list-head${!sorted ? '' : (sortDown ? ' sort-down' : ' sort-up') }`}
      onClick={this.handleHeadClick}
    >{this.getTitle(field)}</span>
  }

  getHeaders() {
    const fields = this.props.shownFields;
    return <li key="head" className="col-header">
      {fields.map(field => this.makeHeadCol(field, this.getTitle(field)))}
    </li>
  }

  getTitle(name) {
    const Titles = {
      num: '#',
      id: 'ID',
      power: 'ATK',
      supp: 'SUPP',
      range: 'RNG',
      type: 'Types'
    }
    return Titles[name] || properCase(name) || '';
  }

  getField(id, field) {
    //console.log(id, field);
    if (!getCard(id)) return;
    const cardIDs = this.getCardIDs();
    const cardmeta = cardIDs[id];
    const card = getCard(id);

    switch(field) {
      case 'num': return cardmeta.num;
      case 'set': return card.id.substring(0, 3);
      case 'name': return `${card.name}: ${card.title}`;
      case 'cost': return card.promo ? `${card.cost}(${card.promo})` : card.cost;
      case 'color': return card.colors.length ? card.colors.map((c) => getColorIcon(c, 18)) : getColorIcon("none", 18);
      case 'range': return formatRange(card.range[0], card.range[1]);
      case 'type': return Object.keys(card.type).map(type => getTypeIcon(type, 15));
      default: return cardmeta[field] || card[field] || '';
    }
  }

  renderCard(id) {
    if (!getCard(id)) return console.log(id+' Not Found');
    //console.log('Rendering '+id);
    if (this.props.listData[id].hidden) return; //console.log('Card Hidden');
    const selected = this.props.selection === id;
    const fields = this.props.shownFields;
    return <li
      key={id}
      data-id={id}
      onClick={(event) => this.onSelect(event)}
      onMouseOver={this.props.onView ? (event) => this.onView(event) : null}
      onMouseLeave={this.props.onView ? () => this.props.onView() : null}
      className={`card-entry${selected ? ' card-selected' : ''}`}>
      {fields.map(field => this.makeCol(field, this.getField(id, field)))}
    </li>
  }

  render() {
    //console.log('Renders');
    const cardList = this.getCardList();
    const sortField = this.state.sortField;
    return <ul className="list card-list">
      {this.getHeaders()}
      {cardList.sort((a,b) => this.compareFields(sortField, a, b)).map((id) => this.renderCard(id))}
    </ul>
  }
}
CardList.defaultProps = {
  listData: {},
  shownFields: [],
  onSelect: () => console.log('No Select Function'),
}

class DeckList extends CardList {
}

class ResultList extends CardList {
}

class CardView extends React.Component {
  render() {
    let debugLabel = <div id="selected-label" style={{position: "absolute"}}>Selected: {this.props.selectedCard}</div>;
    let id = this.props.viewedCard || this.props.selectedCard;
    return <div id="preview-area" className="area left-area">
      {this.props.debug ? debugLabel : ''}
      <CardImg id={id} />
    </div>
  }
}
CardView.defaultProps = {
  debug: false,
  selectedCard: '',
  viewedCard: ''
}

class DeckView extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      shownFields: ["num", "set", "cost", "color", "name", "power", "supp", "range", "type"]
    };
  }

  handleSelect(id) {
    return this.props.onSelect(id);
  }

  render() {
    //return <CardList />;
    //console.log(this.props.deckList);
    const deckList = this.props.deckList;
    const selection = this.props.selectedCard;
    if (selection && !deckList[selection]) this.handleSelect('');
    return <div id="deckview" className="card-area">
      <DeckList
        listData={deckList}
        shownFields={this.state.shownFields}
        onSelect={(id) => this.handleSelect(id)}
        selection={selection}
      />
    </div>
  }
}

class SearchView extends React.Component {
  constructor(props) {
    super(props);
    this.defaultComp = "0";
    this.state = {
      filterOpen: false,
      shownFields: ["set", "cost", "color", "name", "power", "supp", "range", "type"],
      filterName: '',
      filterTitle: '',
      filterResults: [],
      filterColors: {},
      filterPower: this.defaultComp,
      filterSupport: this.defaultComp,
      filterGender: '',
      filterWeapons: {
        sword: false, lance: false, axe: false, bow: false, fang: false,
        tome: false, staff: false, dragonstone: false, knife: false, fist: false
      },
      filterTypes: {
        beast: false, dragon: false, flier: false,
        armored: false, mirage: false, monster: false
      },
      filterId: '',
      filterRange: this.defaultComp,
      filterSkillText: '',
      filterSkillName: '',
      filterSkillKeyword: '',
      filterDeployCost: this.defaultComp,
      filterPromoCost: '',
      textSkillCost: '',
      textSkillDesc: ''
    }
    for (let color in Colors) {
      this.state.filterColors[color.toLowerCase()] = false;
    }
    let fnList = [
      "handleGenderUpdate", "handleNumberExpUpdate",
      "handleTextInputUpdate", "handleMultiboxUpdate"
    ];
    fnList.forEach(fnName => this[fnName] = this[fnName].bind(this));
  }

  hasSearchConditions() {
    return this.filterGender || this.filterId || this.filterSkillName || this.filterSkillText
      || (this.filterPower !== this.defaultComp) || (this.filterSupport !== this.defaultComp)
      || (this.filterDeployCost !== this.defaultComp) || this.filterPromoCost || this.textSkillCost
      || this.textSkillDesc || Object.values(this.filterTypes).some(val => val)
      || Object.values(this.filterColors).some(val => val) || Object.values(this.filterWeapons).some(val => val);
  }

  toggleFilter() {
    this.setState({
      filterOpen: !this.state.filterOpen
    });
  }

  onCardSelect(id) {
    this.props.onSelect(id);
  }

  onCardView(id) {
    this.props.onHover(id);
  }

  setPromoCost(val) {
    this.setState({ filterPromoCost: val })
  }

  getListData() {
      let listData = {};
      let results = this.state.filterResults;
      results.forEach(id => listData[id] = { hidden: !this.isLegalCard(id) });
      //this.getCardPool().forEach(id => listData[id] = { hidden: !this.isLegalCard(id) || !this.cardMatches(id) })
      return listData;
  }

  getCardPool() {
    return Object.keys(CipherDB);
  }

  isLegalCard(id) {
    let set = id.substring(0, id.indexOf('-'));
    let rule = this.props.formatRule;
    if (!rule || !Rules[rule]) return true;
    if (Rules[rule].banned) {
      return !Rules[rule].banned.has(set);
    }
    if (Rules[rule].allowed) {
      return Rules[rule].allowed.has(set);
    }
    return true;
  }

  runSearch() {
    const cardPool = this.getCardPool().slice();
    //if (!this.hasSearchConditions()) this.setState({ filterResults: cardPool });
    //console.log('Checking Colors: ', Object.values(this.state.filterColors).some(v => v===true));
    let results = cardPool.filter(id => {
      let card = getCard(id);
      return this.cardMatches(card);
    });
    let selection = this.props.selectedCard;
    if (selection && results.indexOf(selection) < 0) this.onCardSelect('');
    this.setState({
      filterResults: results,
    });
  }

  cardMatches(card) {
    if (typeof card === 'string') card = getCard(card);
    let isMatch = true;
    // Check For Name/Title/ID
    let filterName = this.state.filterName.toLowerCase().trim();
    let punc = [':',',']; // "Name:" matches "Name" exactly.
    if (punc.indexOf(filterName[filterName.length-1]) >= 0) {
      filterName = filterName.substring(0, filterName.length-1);
      if (card.name.toLowerCase() !== filterName) return false;
    } else if (!card.name.toLowerCase().startsWith(filterName)) return false;
    //&& !console.log(filterName) && card.name.toLowerCase() !== filterName.substring(0, filterName.length-2)) return false;
    if (!card.title.toLowerCase().includes(this.state.filterTitle.toLowerCase())) return false;
    if (!card.id.toLowerCase().includes(this.state.filterId.toLowerCase())) return false;
    // Check Cost / Promo Cost
    if (!numInRange(card.cost, this.state.filterDeployCost)) return false;
    let promoCheck = !!this.state.filterPromoCost;
    if (promoCheck) {
      if (!card.promo || !numInRange(card.promo, this.state.filterPromoCost)) return false;
    }
    // Check Power/Supp
    if (!numInRange(card.power, this.state.filterPower)) return false;
    if (!numInRange(card.supp, this.state.filterSupport)) return false;
    // Check Range
    let rangeReq = this.state.filterRange;
    if (rangeReq && !numInRange(card.range[0], rangeReq) && !numInRange(card.range[1], rangeReq)) return false;
    // Check Color
    //console.log('Matched Name')
    const filterColors = this.state.filterColors;
    const colorReq = Object.keys(filterColors).filter(c => filterColors[c]);
    //console.log(colorReq);
    if (colorReq.length) {
      //console.log('Color Check');
      let meetsReq = colorReq.some(c => this.cardMatchesColor(card, c));
      if (!meetsReq) return false;
    }
    // Check Gender
    const genderReq = this.state.filterGender;
    if (genderReq) {
      if (genderReq === "none") {
        if (card.gender.length > 0) return false;
      } else if (!card[genderReq]) return false;
    }
    // Check Weapon
    const filterWeapons = this.state.filterWeapons;
    const wepReq = Object.keys(filterWeapons).filter(i => filterWeapons[i]);
    if (wepReq.length) {
      let meetsReq = wepReq.some(i => this.cardMatchesWeapon(card, i));
      if (!meetsReq) return false;
    }
    // Check Type
    const filterTypes = this.state.filterTypes;
    const typeReq = Object.keys(filterTypes).filter(i => filterTypes[i]);
    if (typeReq.length) {
      let meetsReq = typeReq.every(i => this.cardMatchesType(card, i));
      if (!meetsReq) return false;
    }
    // Check Skills
    //console.log(isMatch, 'skill check')
    let costCheck = this.state.textSkillCost.toLowerCase();
    let descCheck = this.state.textSkillDesc.toLowerCase();
    let textCheck = this.state.filterSkillText.toLowerCase();
    let nameCheck = this.state.filterSkillName.toLowerCase();
    let keyCheck  = this.state.filterSkillKeyword.toLowerCase();
    let needSkillCheck = (costCheck || descCheck || textCheck || nameCheck || keyCheck);
    if (needSkillCheck) {
      isMatch = isMatch && card.skills.some(skill => {
        if (keyCheck && (!skill.keyword || !skill.keyword.toLowerCase().includes(keyCheck))) return false;
        if (nameCheck && !skill.name.toLowerCase().includes(nameCheck)) return false;
        if (costCheck && !skill.costs.some(cost => cost.toLowerCase().includes(costCheck))) return false;
        if (descCheck && !skill.desc.toLowerCase().includes(descCheck)) return false;
        if (textCheck) {
          if (!skill.desc.toLowerCase().includes(textCheck) && !skill.costs.some(cost => cost.toLowerCase().includes(textCheck))) {
            return false;
          }
        }
        return true;
      });
    }

    return isMatch;
  }

  cardMatchesColor(card, color) {
    if (typeof card === 'string') card = getCard(card);
    switch (color) {
      case "none": return card.colors.length === 0;
      case "whiteblack":
        return card.colors.indexOf("white") >= 0 && card.colors.indexOf("black") >= 0;
      default:
        return card.colors.indexOf(color) >= 0;
    }
  }

  cardMatchesWeapon(card, weapon) {
    if (typeof card === 'string') card = getCard(card);
    return card.weapon.indexOf(weapon) >= 0;
  }

  cardMatchesType(card, type) {
    if (typeof card === 'string') card = getCard(card);
    return card.type[type];
  }

  render() {
    return <div id="bottom-area">
      <FilterView
        filterName={this.state.filterName}
        filterTitle={this.state.filterTitle}
        filterColors={this.state.filterColors}
        filterGender={this.state.filterGender}
        filterWeapons={this.state.filterWeapons}
        filterTypes={this.state.filterTypes}
        runSearch={() => this.runSearch()}
        onNameChange={(e) => this.handleTextInputUpdate('filterName', e, true)}
        onTitleChange={(e) => this.handleTextInputUpdate('filterTitle', e, true)}
        onColorUpdate={(e) => this.handleMultiboxUpdate('filterColors', e, true)}
        onGenderUpdate={this.handleGenderUpdate}
        onWeaponUpdate={(e) => this.handleMultiboxUpdate('filterWeapons', e, true)}
        onTypeUpdate={(e) => this.handleMultiboxUpdate('filterTypes', e, true)}
        toggleAdvSearch={() => this.toggleFilter()}
       />
      <ResultView
        shownFields={this.state.shownFields}
        selectedCard={this.props.selectedCard}
        onHover={(id) => this.onCardView(id)}
        onSelect={(id) => this.onCardSelect(id)}
        listData={this.getListData()}
      />
      <Modal
        show={this.state.filterOpen}
        buttons={[{ name: "Search", close: true, func: () => this.runSearch()}]}
        onClose={() => this.toggleFilter()}>
        <FilterPopup
          setPromoCost={(val) => this.setPromoCost(val)}
          skillText={this.state.filterSkillText}
          skillName={this.state.filterSkillName}
          skillCostText={this.state.textSkillCost}
          skillDescText={this.state.textSkillDesc}
          skillKeyword={this.state.filterSkillKeyword}
          filterDeployCost={this.state.filterDeployCost}
          filterPromoCost={this.state.filterPromoCost}
          filterId={this.state.filterId}
          filterPower={this.state.filterPower}
          filterSupport={this.state.filterSupport}
          filterRange={this.state.filterRange}
          changeSkillText={(e) => this.handleTextInputUpdate('filterSkillText', e)}
          changeSkillName={(e) => this.handleTextInputUpdate('filterSkillName', e)}
          changeSkillCostText={(e) => this.handleTextInputUpdate('textSkillCost', e)}
          changeSkillDescText={(e) => this.handleTextInputUpdate('textSkillDesc', e)}
          changeSkillKeyword={(e) => this.handleTextInputUpdate('filterSkillKeyword', e)}
          changeDeployCost={(e) => this.handleNumberExpUpdate('filterDeployCost', e)}
          changePromoCost={(e) => this.handleNumberExpUpdate('filterPromoCost', e)}
          changeId={(e) => this.handleTextInputUpdate('filterId', e)}
          changeRange={(e) => this.handleNumberExpUpdate('filterRange', e)}
          changePower={(e) => this.handleNumberExpUpdate('filterPower', e)}
          changeSupport={(e) => this.handleNumberExpUpdate('filterSupport', e)}
        />
      </Modal>
    </div>
  }

  handleGenderUpdate(event) {
    this.setState({ filterGender: event.target.checked ? event.target.value : ''}, this.runSearch);
  }

  handleMultiboxUpdate(name, event, runSearch) {
    if (!(name in this.state)) return false;
    const newData = Object.assign({}, this.state[name]);
    newData[event.target.id.split('-')[0]] = event.target.checked;
    this.setState({ [name]: newData }, !runSearch ? null : this.runSearch);
  }

  handleTextInputUpdate(name, event, runSearch) {
    if (!(name in this.state)) return false;
    this.setState({ [name]: event.target.value }, !runSearch ? null : this.runSearch);
  }

  handleNumberExpUpdate(name, event, runSearch) {
    if (!this.state[name]) return console.log(`${name} not found...`);
    let newValue = "";
    const exp = this.state[name];
    if (event.target.tagName === "SELECT") newValue = event.target.value + exp.substring(1);
    else newValue = exp[0]+event.target.value;
    this.setState({ [name]: newValue }, !runSearch ? null : this.runSearch);
  }
}

class FilterView extends React.Component {
  getColorSelectors() {
    const checks = Object.keys(Colors).map(color => <React.Fragment key={color}>
      <img src={Colors[color]} alt={color} width="20px"></img>
      <input
        id={color.toLowerCase()+'-colorcheck'}
        className="color-check"
        type="checkbox"
        onChange={this.props.onColorUpdate}
        value={color}
      />
    </React.Fragment>);
    checks.splice(5, 0, <br key="color-break" />);
    return checks;
  }

  getGenderSelectors() {
    return <React.Fragment>
      {getTypeIcon('male', 20)}
      <input
        id="m-gendercheck"
        className="gender-check"
        value="male"
        type="checkbox"
        onChange={this.props.onGenderUpdate}
        checked={this.props.filterGender === 'male'}
      />
      {getTypeIcon('female', 20)}
      <input
        id="f-gendercheck"
        className="gender-check"
        value="female"
        type="checkbox"
        onChange={this.props.onGenderUpdate}
        checked={this.props.filterGender === 'female'}
      />
      <span style={({
        display: "inline-block",
        width: "20px",
        "textAlign": "center",
        "verticalAlign": "top"
      })}>N</span>
      <input
        id="n-gendercheck"
        className="gender-check"
        value="none"
        type="checkbox"
        onChange={this.props.onGenderUpdate}
        checked={this.props.filterGender === 'none'}
      />
    </React.Fragment>
  }

  getWeaponSelectors() {
    const weapons = this.props.filterWeapons;
    const checks = Object.keys(weapons).map(weapon => (<React.Fragment key={weapon}>
      {getTypeIcon(weapon, 20)}
      <input
        key={weapon}
        id={weapon+'-wepcheck'}
        className="weapon-check"
        type="checkbox"
        onChange={this.props.onWeaponUpdate}
        value={weapon}
      />
    </React.Fragment>));
    checks.splice(5, 0, <br key="weapon-break" />);
    return checks;
  }

  getTypeSelectors() {
    const types = this.props.filterTypes;
    const checks = Object.keys(types).map(type => <React.Fragment key={type}>
      {getTypeIcon(type, 20)}
      <input
        id={type+'-typecheck'}
        className="type-check"
        type="checkbox"
        onChange={this.props.onTypeUpdate}
        value={type}
      />
    </React.Fragment>);
    checks.splice(3, 0, <br key="type-break" />);
    return checks;
  }

  render() {
    //console.log(this.props);
    return <div id="search-area" className="area left-area">
      <div id="search-area-header">Search</div>
      <div id="search-area-fields">
        <div>
          <span>Name:</span>
          <input
            type="text"
            id="search-name"
            value={this.props.filterName}
            onChange={this.props.onNameChange}
          />
        </div>
        <div>
          <span>Title:</span>
          <input
            type="text"
            id="search-title"
            value={this.props.filterTitle}
            onChange={this.props.onTitleChange}
          />
        </div>
        <div>{this.getColorSelectors()}</div>
        <div>{this.getGenderSelectors()}</div>
        <div>{this.getWeaponSelectors()}</div>
        <div>{this.getTypeSelectors()}</div>
        <button onClick={() => this.props.toggleAdvSearch()}>Advanced Search</button>
      </div>
    </div>
  }
}

