class DeckControl extends React.Component {
  constructor(props) {
    super(props);
    this.ruleOptions = Object.keys(Rules).map(name => <option key={name}>{name}</option>);
    this.handleRuleChange = this.handleRuleChange.bind(this);
  }

  handleRuleChange(event) {
    this.props.changeRule(event.target.value);
  }

  addCard(num) {
    return this.props.addSelectedCard(num);
  }

  setCard(num) {
    return this.props.setSelectedCard(num);
  }

  toggleTextDiv() {
    return this.props.toggleImport();
  }


  render() {
    return <div id="deckcontrol">
      <button onClick={() => this.addCard(1)}>+1</button>
      <button onClick={() => this.addCard(-1)}>-1</button>
      <button onClick={() => this.setCard(4)}>=4</button>
      <button onClick={() => this.setCard(0)}>-ALL</button>
      <button onClick={() => this.toggleTextDiv()}>Import/Export</button>
      <select value={this.props.formatRule} onChange={this.handleRuleChange}>{this.ruleOptions}</select>
    </div>
  }
}

class DeckBuilder extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      formatRule: "Standard",
      deckList: {  },
      viewedCard: '',
      selectionArea: '',
      selectedCard: '',
      importOpen: false,
      deckTextInput: '',
    }
  }

  changeRule(name) {
    if (Rules[name]) this.setState({ formatRule: name });
  }

  toggleImport() {
    this.setState({
      deckTextInput: this.getDeckText(),
      importOpen: !this.state.importOpen
    });
  }

  updateDeckText(text) {
    this.setState({
      deckTextInput: text
    });
  }

  loadLackeyDeck(text) {
    let deck = {};
    let entries = text.split('\n');
    let mc = false;
    for (let line of entries) {
      if (line.startsWith('MC:')) {
        mc = true;
        continue;
      }
      line = line.trim();
      if (!line) continue;
      let sep = line.indexOf('\t');
      let num = Math.trunc(line.substring(0, sep));
      let cardName = line.substring(sep+1);
      let id = convertLackeyName(cardName);
      if (!id) continue;
      if (mc && deck[id]) {
          deck[id].mc = true;
          deck[id].num += num;
      } else {
        deck[id] = { num: num , mc: mc }
      }
    }
    if (Object.keys(deck).length) {
      this.setState({
        deckList: deck
      })
    }
  }

  selectCard(area, id) {
    if (!id || (id === this.state.selectedCard && this.isAreaSelected(area))) this.clearSelection();
    else this.setState({
      selectionArea: area,
      selectedCard: id,
    });
  }

  viewCard(id) {
    this.setState({
      viewedCard: getCard(id) ? id : '',
    });
  }

  clearSelection() {
    this.setState({
      selectionArea: '',
      selectedCard: '',
    });
  }

  isAreaSelected(area) {
    return this.state.selectionArea === area;
  }

  getSelectedCard() {
    return this.state.selectedCard;
  }

  getSelectedCardDetails() {
    return `${this.state.selectionArea} ${this.state.selectedCard}`;
  }

  getDeckList() {
    return this.state.deckList;
  }

  getDeckText() {
    let txt = "";
    let cardText = [];
    const deckList = this.getDeckList();
    for (let id in deckList) {
      let card = getCard(id);
      let meta = deckList[id];
      if (!card) continue;
      cardText.push(`${meta.num}\t${getLackeyName(id)}`);
    }
    return cardText.join('\n');
  }

  setCard(id, num) { // Set Card Amount To #
    if (!id) return console.log('No Card Selected');
    if (!getCard(id)) return console.log('Card Not Found', id);
    num = Math.trunc(num);
    if (isNaN(num) || num < 0) return;
    const deckList = this.getDeckList();
    console.log(deckList);
    if (deckList[id]) {
      let curr = deckList[id].num;
      this.addCard(id, num - curr);
    } else {
      this.addCard(id, num);
    }
  }

  addCard(id, num) { //  Add # of Card
    if (!id) return console.log('No Card Selected');
    if (!getCard(id)) return console.log('Card Not Found', id);
    num = Math.trunc(num);
    if (isNaN(num) || num === 0) return;
    if (num < 0) return this.remCard(id, -num);
    if (num > 99) num = 99;
    const cardIDs = Object.assign({}, this.state.deckList);
    let changed = true;
    if (id in cardIDs) {
      let prev = cardIDs[id].num;
      cardIDs[id].num += num;
      if (cardIDs[id].num > 99) cardIDs[id].num = 99;
      changed = prev !== cardIDs[id].num;
    } else {
      cardIDs[id] = { num: num, mc: false };
    }
    if (changed) this.setState({
      deckList: cardIDs
    });
  }

  remCard(id, num) {
    if (!getCard(id)) return console.log('Card Not Found', id);
    num = Math.trunc(num);
    if (isNaN(num) || num === 0) return;
    if (num < 0) return this.addCard(id, -num);
    const cardIDs = Object.assign({}, this.state.deckList);
    if (id in cardIDs) {
      let cardmeta = cardIDs[id];
      cardmeta.num -= num;
      if (cardmeta.num < 1) delete cardIDs[id];
      return this.setState({
        deckList: cardIDs
      });
    }
  }

  render() {
    const importButtons = [
      {
        name: "Import",
        func: () => this.loadLackeyDeck(this.state.deckTextInput),
        close: true
      }
    ];

    return <React.Fragment>
      <div id="deckbuilder">
        <div id="top-area">
          <CardView
            viewedCard={this.state.viewedCard}
            selectedCard={this.getSelectedCard()}
          />
          <div id="right-pane" className="area right-area">
            <DeckView
              deckList={this.state.deckList}
              selectedCard={this.isAreaSelected('deck') ? this.getSelectedCard() : '' }
              onHover={(id) => this.viewCard(id)}
              onSelect={ (id) => this.selectCard('deck', id) }
            />
            <DeckControl
              formatRule={this.state.formatRule}
              addSelectedCard={(num) => this.addCard(this.getSelectedCard(), num)}
              setSelectedCard={(num) => this.setCard(this.getSelectedCard(), num)}
              toggleImport={() => this.toggleImport()}
              changeRule={(name) => this.changeRule(name)}
            />
          </div>
        </div>
        <SearchView
          formatRule={this.state.formatRule}
          selectedCard={this.isAreaSelected('search') ? this.getSelectedCard() : '' }
          toggleFilter={() => this.toggleFilter()}
          onHover={(id) => this.viewCard(id)}
          onSelect={ (id) => this.selectCard('search', id) }
        />
      </div>
      <div id="details-pane">
        <CardDetailView
          id="selected-view"
          card={this.state.selectedCard}
        />
        <CardDetailView
          id="hovered-view"
          card={this.state.viewedCard === this.state.selectedCard ? '' : this.state.viewedCard}
        />
      </div>
      <Modal
        show={this.state.importOpen}
        buttons={importButtons}
        onClose={() => this.toggleImport()}>
        <DeckTextField
          deckText={this.state.deckTextInput}
          updateDeckText={(text) => this.updateDeckText(text)}
        />
      </Modal>
    </React.Fragment>
  }
}

class CardDetailView extends React.Component {

  formatSkill(skill) {
    let head = "";
    if (skill.keyword) head += `[${skill.keyword}] `;
    head += `${skill.name} | ${skill.trigger.toUpperCase()} |`;
    if (skill.opt) head += " [OPT]"
    if (skill.costs.length) head += ` ${skill.costs.join(", ")}`;
    //return ([head, <br></br>, skill.desc, <br></br>])
    return <div key={skill.name} className="skill-container">{head}<br></br>{skill.desc}</div>;
  }

  render() {
    if (!this.props.card) return null;
    const card = getCard(this.props.card);
    if (!card) return null;
    return <div id={this.props.id} className="card-details">
      {card.id}<br></br>
      {card.name}: {card.title}
      <br></br>
      {card.colors.map(c => properCase(c)).join('/') || 'Colorless'} {formatCost(card.cost, card.promo)}
      <br></br>
      Power: {`${card.power}(${card.supp})`} Range {formatRange(card.range[0], card.range[1])}
      <br></br>
      Skills:
      {card.skills.map(this.formatSkill)}
    </div>
  }
}

class DeckTextField extends React.Component {
  constructor(props) {
    super(props);
    this.onChange = this.onChange.bind(this);
  }

  getContent() {
    return this.props.deckText;
  }

  onChange(event) {
    this.props.updateDeckText(event.target.value);
  }

  render() {
    return <textarea
      id="deck-import"
      onChange={this.onChange}
      value={this.getContent()}
    />
  }
}

class Modal extends React.Component {
  close() {
    this.props.onClose();
  }

  renderButtons() {
    if (!this.props.buttons) return null;
    const buttons = this.props.buttons;
    return buttons.map(button => <button
      key={button.name}
      data-close={!!button.close}
      onClick={(e) => {
        button.func();
        if (e.target.dataset.close === "true") this.close();
      }}>
      {button.name}
    </button>);
  }

  render() {
    // Render nothing if the "show" prop is false
    if(!this.props.show) {
      return null;
    }

    // The gray background
    const backdropStyle = {
      position: 'absolute',
      top: "50%",
      left: "50%",
      transform: "translate(-50%, -50%)",
      backgroundColor: 'rgba(0,0,0,0.3)',
      padding: 50
    };

    // The modal "window"
    const modalStyle = {
      backgroundColor: '#fff',
      borderRadius: 5,
      maxWidth: 500,
      minHeight: 300,
      margin: '0 auto',
      padding: 30,
      "zIndex": 10
    };

    return (
      <div className="backdrop" style={backdropStyle}>
        <div className="modal" style={modalStyle}>
          {this.props.children}

          <div className="footer">
            <button onClick={this.props.onClose}>
              Close
            </button>
            {this.renderButtons()}
          </div>
        </div>
      </div>
    );
  }
}

class App extends React.Component {
  render() {
    return <DeckBuilder />
  }
}

export default App;
